/* Styles applied to form */
#ModelDescription form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    border: 1px solid black;
    align-self: center;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}

#ModelDescription form div:nth-child(1) {
    grid-row: 1 / 3;
    
}

#ModelDescription form>div {
    margin: 2vh 2vw;
}

/* Styles applied to radio buttons */
#ModelDescription .MuiRadio-root {
    padding: 1px 0 1px 9px;
}

#ModelDescription .MuiTypography-body1 {
    font-size: 0.9em;
}

/* Styles applied to input */
#ModelDescription .MuiInputBase-root {
